h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.filtre_vert {
  background-color: #416c25;
  color: #fff;
  text-align: center;
}

.filtre_vert a {
  color: inherit;
}

.menu, .bandeau_menu {
  display: flex;
  justify-content: space-around;
}

.entree_menu {
  padding: 10px;
}

.bandeau_menu {
  color: #000;
  text-align: center;
}

.bandeau_menu a {
  color: inherit;
}

.bandeau_menu .image_ab_bandeau {
  flex-grow: 0;
}

.bandeau_menu div {
  flex-grow: 1;
}

.image_ab_bandeau img {
  height: 44px;
}

.cadre_vert_menu, .cadre_vert {
  background-color: #79b51d;
}

.cadre_jaune_menu, .cadre_jaune {
  background-color: #f6cc08;
}

.img_fond img {
  width: 100%;
}

.row {
  display: flex;
  margin: 10px;
}

.row_bandes {
  height: 20px;
  margin: 0 0 50px 0;
}

.row_bandes div {
  width: 50%;
}

.actus {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.img-actu {
  width: 40%;
  margin-right: 20px;
}

.img-actu img {
  width: 100%;
}

.texte_actu {
  width: 60%;
}
